import React from 'react'
import { useSelector } from 'react-redux';

export default function Users() {
    const { users } = useSelector(state => state)

    return (
        <div>
            <table className="table table-dark">
                <thead>
                    <tr>
                        <th>Username</th>
                        <th>Nombre</th>
                        <th>email</th>
                    </tr>
                </thead>
                <tbody>
                {users.map(user => 
                    (
                            <tr key={user.id}>
                                <td>{user.username}</td>
                                <td>{user.name}</td>
                                <td>{user.email}</td>
                            </tr>
                    )
                    )}
                    </tbody>
            </table>

        </div>
    )
}
